import { NextResponse } from "next/server";
import connectMongo from "@/lib/mongoose";
import ChatHistory from "@/models/ChatHistory";

export async function GET(request) {
	try {
		await connectMongo();

		const { searchParams } = new URL(request.url);
		const userEmail = searchParams.get("userEmail");
		const userId = searchParams.get("userId");
		const limit = parseInt(searchParams.get("limit")) || 20;

		// ÊîØÊè¥ÂÖ©Á®ÆÁî®Êà∂Ë≠òÂà•ÊñπÂºèÔºöuserEmail Êàñ userId
		const userIdentifier = userEmail || userId;
		if (!userIdentifier) {
			return NextResponse.json(
				{ error: "Áî®Êà∂Ë≠òÂà•ÂèÉÊï∏‰∏çËÉΩÁÇ∫Á©∫ (userEmail Êàñ userId)" },
				{ status: 400 }
			);
		}

		console.log("üìö ÂæûChatHistoryÁç≤ÂèñÁî®Êà∂Â∞çË©±Ê≠∑Âè≤:", userIdentifier);

		// ÂæûChatHistoryÁç≤ÂèñÂ∞çË©±Ê≠∑Âè≤
		const query = userEmail ? { userEmail: userEmail } : { userId: userId };

		const chatHistories = await ChatHistory.find(query)
			.sort({ updatedAt: -1 })
			.limit(limit)
			.lean();

		console.log(`üìä ÊâæÂà∞ ${chatHistories.length} ÂÄãÂ∞çË©±Ë®òÈåÑ`);

		const conversationSummaries = chatHistories.map((chat) => {
			const lastMessage = chat.messages?.[chat.messages.length - 1];
			const lastUserMessage = chat.messages
				?.filter((msg) => msg.role === "user")
				?.slice(-1)[0];

			return {
				conversationId: chat.conversationId,
				sessionId: chat.sessionId || chat.conversationId,
				title:
					chat.title ||
					generateConversationTitle(
						chat.primaryConcern,
						lastUserMessage?.content
					),
				primaryConcern: chat.primaryConcern || "‰∏ÄËà¨Ë´ÆË©¢",
				messageCount: chat.messages?.length || 0,
				lastUpdated: chat.updatedAt,
				lastActivity: chat.updatedAt,
				conversationState: chat.conversationState,
				preview: generatePreview(lastUserMessage?.content || ""),
				hasDetailedHistory: true, // ChatHistoryÁ∏ΩÊòØÊúâË©≥Á¥∞Ê≠∑Âè≤
				topics:
					chat.context?.topics ||
					[chat.primaryConcern].filter(Boolean),
			};
		});

		return NextResponse.json({
			success: true,
			userIdentifier: userIdentifier,
			totalConversations: conversationSummaries.length,
			conversations: conversationSummaries,
		});
	} catch (error) {
		console.error("‚ùå Áç≤ÂèñÂ∞çË©±Ê≠∑Âè≤Â§±Êïó:", error);
		return NextResponse.json(
			{
				error: "Áç≤ÂèñÂ∞çË©±Ê≠∑Âè≤Â§±Êïó",
				details: error.message,
			},
			{ status: 500 }
		);
	}
}

// ËºîÂä©ÂáΩÊï∏ÔºöÁîüÊàêÂ∞çË©±Ê®ôÈ°å - È°ØÁ§∫ÂØ¶ÈöõÁî®Êà∂ÂïèÈ°å
function generateConversationTitle(primaryConcern, lastMessage) {
	// ÂÑ™ÂÖà‰ΩøÁî®ÂØ¶ÈöõÁöÑÁî®Êà∂ÂïèÈ°å‰ΩúÁÇ∫Ê®ôÈ°å
	if (lastMessage && typeof lastMessage === "string" && lastMessage.trim()) {
		// Ê∏ÖÁêÜÂíåÊà™ÂèñÁî®Êà∂ÂïèÈ°åÔºåÁî®‰ΩúÊ®ôÈ°å
		const cleanedMessage = lastMessage.replace(/\s+/g, " ").trim();

		// Â¶ÇÊûúÊ∂àÊÅØÂ§™Èï∑ÔºåÊà™ÂèñÂâç40ÂÄãÂ≠óÁ¨¶‰∏¶Âä†‰∏äÁúÅÁï•Ëôü
		if (cleanedMessage.length > 40) {
			return cleanedMessage.substring(0, 40) + "...";
		}

		return cleanedMessage;
	}

	// ÂÇôÁî®ÊñπÊ°àÔºö‰ΩøÁî®ÂÇ≥Áµ±ÁöÑÂàÜÈ°ûÊ®ôÈ°å
	const concernTitles = {
		ÊÑüÊÉÖ: "ÊÑüÊÉÖË´ÆË©¢",
		Â∑•‰Ωú: "Â∑•‰ΩúÈÅãÂã¢",
		Ë≤°ÈÅã: "Ë≤°ÈÅãÂàÜÊûê",
		ÂÅ•Â∫∑: "ÂÅ•Â∫∑ÈÅãÂã¢",
		‰∫∫Èöõ: "‰∫∫ÈöõÈóú‰øÇ",
		Â≠êÂ•≥: "Â≠êÂ•≥ÈÅãÂã¢",
		Â±ÖÂÆ∂‰ΩàÂ±Ä: "Â±ÖÂÆ∂È¢®Ê∞¥",
	};

	if (primaryConcern && concernTitles[primaryConcern]) {
		return concernTitles[primaryConcern];
	}

	return "È¢®Ê∞¥Ë´ÆË©¢";
}

// ËºîÂä©ÂáΩÊï∏ÔºöÁîüÊàêÈ†êË¶ΩÊñáÂ≠ó
function generatePreview(message) {
	if (!message) return "Êö´ÁÑ°ÂÖßÂÆπ";

	// Ê∏ÖÁêÜÊñáÂ≠ó‰∏¶Êà™ÂèñÂâç30ÂÄãÂ≠óÁ¨¶
	const cleaned = message.replace(/\s+/g, " ").trim();
	return cleaned.length > 30 ? cleaned.substring(0, 30) + "..." : cleaned;
}

// ËºîÂä©ÂáΩÊï∏ÔºöÊèêÂèñË©±È°åÊ®ôÁ±§
function extractTopics(primaryConcern, lastMessage) {
	const topics = [];

	// ‰∏ªË¶ÅÈóúÊ≥®È†òÂüü
	if (primaryConcern) {
		topics.push(primaryConcern);
	}

	// ÂæûË®äÊÅØÂÖßÂÆπÊèêÂèñÂÖ∂‰ªñË©±È°å
	if (lastMessage) {
		const topicKeywords = {
			ÊÑüÊÉÖ: ["ÊÑüÊÉÖ", "ÊÑõÊÉÖ", "Ê°ÉËä±", "ÂßªÁ∑£", "ÊàÄÊÑõ", "ÂàÜÊâã", "ÁµêÂ©ö"],
			Â∑•‰Ωú: ["Â∑•‰Ωú", "‰∫ãÊ•≠", "ËÅ∑Â†¥", "ÂçáËÅ∑", "Ë∑≥ÊßΩ", "ÂâµÊ•≠"],
			Ë≤°ÈÅã: ["Ë≤°ÈÅã", "Ë≥∫Èå¢", "ÊäïË≥á", "ÁêÜË≤°", "Êî∂ÂÖ•", "ÈáëÈå¢"],
			ÂÅ•Â∫∑: ["ÂÅ•Â∫∑", "Ë∫´È´î", "È§äÁîü", "ÁóÖÁóõ", "ÈÜ´ÁôÇ"],
			‰∫∫Èöõ: ["‰∫∫Èöõ", "ÊúãÂèã", "Âêå‰∫ã", "Âêà‰Ωú", "Á§æ‰∫§"],
			Â≠êÂ•≥: ["Â≠êÂ•≥", "Â≠©Â≠ê", "ÊïôËÇ≤", "Â≠∏Áøí", "ËÄÉË©¶"],
			Â±ÖÂÆ∂: ["Â±ÖÂÆ∂", "ÊàøÂ≠ê", "Êê¨ÂÆ∂", "Ë£ù‰øÆ", "‰ΩàÂ±Ä"],
			ÂÖ´Â≠ó: ["ÂÖ´Â≠ó", "ÂëΩÁõ§", "‰∫îË°å", "Â§©Âπ≤", "Âú∞ÊîØ"],
		};

		for (const [topic, keywords] of Object.entries(topicKeywords)) {
			if (keywords.some((keyword) => lastMessage.includes(keyword))) {
				if (!topics.includes(topic)) {
					topics.push(topic);
				}
			}
		}
	}

	return topics.slice(0, 3); // ÊúÄÂ§öËøîÂõû3ÂÄãË©±È°å
}

// ËºîÂä©ÂáΩÊï∏ÔºöÁç≤ÂèñÊúÄÂæå‰∏ÄÊ¢ùÁî®Êà∂Ê∂àÊÅØ - ÊîØÊåÅÂÖ©Á®ÆÊï∏ÊìöÊ†ºÂºè
function getLastUserMessage(lastMessage) {
	if (!lastMessage) return null;

	// Êñ∞Ê†ºÂºèÔºörole/content
	if (lastMessage.role && lastMessage.content) {
		return lastMessage.role === "user" ? lastMessage.content : null;
	}

	// ËàäÊ†ºÂºèÔºöuserMessage/assistantMessage
	if (lastMessage.userMessage) {
		return lastMessage.userMessage;
	}

	return null;
}
